
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { mapGetters } from "vuex";
import Cropper from "cropperjs"; 
import api from "../../api/index.js";
import axios from "axios";

export default {
directives: {
TransferDom
},
mounted() {

var self = this;
var image = document.getElementById("image1");
this.cropper = new Cropper(image, {
aspectRatio: 1,
viewMode: 1,
background: false,
zoomable: false,
ready: function() {
self.croppable = true;
}
});
},
name: "Info",
components: {
XHeader,
Actionsheet,
Group,
Cell,
Popup
},
data() {
return {
menus: {
menuSelect: "拍照",
menuCapture: "从相册选取"
},
showMenus: false,

userData: {},
baseURL: "",
base64: "",
headerImage: require("./img/default-user.png"),
jobnull: "无",
picValue: "",
cropper: "",
croppable: false,
panel: false,
url: ""
};
},

methods: {
getPicture() {
navigator.camera.getPicture(
this.onPictureSuccess,
this.onPictureFailure,
{
quality: 50,
sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
destinationType: Camera.DestinationType.FILE_URI,
saveToPhotoAlbum: true
}
);
},
onPictureSuccess(imageData) {


this.uploadImage(imageData);

},
onPictureFailure(message) {
console.log(message);
},
getPictureCapture() {
navigator.camera.getPicture(
this.onPictureCaptureSuccess,
this.onPictureFailure,
{}
);
},
getPictureCapture() {
navigator.camera.getPicture(
this.onPictureCaptureSuccess,
this.onPictureFailure,
{
quality: 50,
sourceType: Camera.PictureSourceType.CAMERA,
destinationType: Camera.DestinationType.FILE_URI,
saveToPhotoAlbum: true
}
);
},
onPictureCaptureSuccess(imageData) {


this.uploadImage(imageData);

},
uploadImage(image) {
var self = this;
var formData = new FormData();

window.resolveLocalFileSystemURL(
image,
function(fileEntry) {
fileEntry.file(
function(file) {


var reader = new FileReader();

reader.readAsDataURL(file);

reader.onload = function(e) {
var src = e.target.result;

var data;
var img = new Image();
img.src = src;

img.onload = function() {
if (src.length <= 100 * 1024) {
data = src;
} else {
data = self.compress(img);
}

self.panel = true;
self.picValue = file;


self.url = data;


if (self.cropper) {
self.cropper.replace(self.url);
}
};
};
reader.onloadend = function(e) {};
},
function(e) {

}
);
},
function(e) {}
);
},
getObjectURL(file) {
var url = null;
if (window.createObjectURL != undefined) {

url = window.createObjectURL(file);
} else if (window.URL != undefined) {

url = window.URL.createObjectURL(file);
} else if (window.webkitURL != undefined) {

url = window.webkitURL.createObjectURL(file);
}
return url;
},
change(e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.panel = true;
this.picValue = files[0];

this.url = this.getObjectURL(this.picValue);


if (this.cropper) {
this.cropper.replace(this.url);
}
},
crop() {
this.panel = false;
var croppedCanvas;
var roundedCanvas;

if (!this.croppable) {
return;
}

croppedCanvas = this.cropper.getCroppedCanvas();


roundedCanvas = this.getRoundedCanvas(croppedCanvas);

this.headerImage = roundedCanvas.toDataURL();

this.postImg();
},
getRoundedCanvas(sourceCanvas) {
var canvas = document.createElement("canvas");
var context = canvas.getContext("2d");
var width = sourceCanvas.width;
var height = sourceCanvas.height;

canvas.width = width;
canvas.height = height;

context.imageSmoothingEnabled = true;
context.drawImage(sourceCanvas, 0, 0, width, height);
context.globalCompositeOperation = "destination-in";
context.beginPath();
context.arc(
width / 2,
height / 2,
Math.min(width, height) / 2,
0,
2 * Math.PI,
true
);
context.fill();

return canvas;
},
dataURItoBlob(dataUrl) {
var byteString = atob(dataUrl.split(",")[1]);
var mimeString = dataUrl
.split(",")[0]
.split(":")[1]
.split(";")[0];
var ab = new ArrayBuffer(byteString.length);
var ia = new Uint8Array(ab);
for (var i = 0; i < byteString.length; i++) {
ia[i] = byteString.charCodeAt(i);
}
return new Blob([ab], { type: mimeString });
},
postImg() {

const _this = this;

let config = {
headers: {
"Content-Type": "multipart/form-data"
}
};
let userid = window.localStorage.getItem("user");

let uploadData = new FormData();
uploadData.append(
"image",
this.dataURItoBlob(this.headerImage),
"image.jpeg"
);
uploadData.append("id", userid);

let ax = axios.create();
ax.post("/user/index/add", uploadData, config).then(res => {
if (res.data.code === 1) {
this.$vux.toast.text("上传成功", "middle");
} else {
this.$vux.toast.text(res.data.msg, "middle");
}
});
},
compress(img) {
let canvas = document.createElement("canvas");

let context = canvas.getContext("2d");



var originWidth = img.width;
var originHeight = img.height;
let initSize = img.src.length;

var maxWidth = 800,
maxHeight = 800;

var targetWidth = originWidth,
targetHeight = originHeight;

if (originWidth > maxWidth || originHeight > maxHeight) {
if (originWidth / originHeight > maxWidth / maxHeight) {

targetWidth = maxWidth;
targetHeight = Math.round(maxWidth * (originHeight / originWidth));
} else {
targetHeight = maxHeight;
targetWidth = Math.round(maxHeight * (originWidth / originHeight));
}
}

canvas.width = targetWidth;
canvas.height = targetHeight;

context.clearRect(0, 0, targetWidth, targetHeight);

context.drawImage(img, 0, 0, targetWidth, targetHeight);
















































let ndata = canvas.toDataURL("image/jpeg", 0.4);
console.log("压缩前：" + initSize);
console.log("压缩后：" + ndata.length);
console.log(
"压缩率：" + ~~(100 * (initSize - ndata.length) / initSize) + "%"
);

return ndata;
}
},
computed: {
...mapGetters(["userInfo"])
},

created() {


},
activated() {
let userid = window.localStorage.getItem("user");
this.$store.dispatch("getInfo", { id: userid });
},

watch: {
userInfo: function(val) {
this.userData = val;
if (val.avatar) {
this.headerImage = val.avatar;
} else {
this.headerImage = require("./img/default-user.png");
}
}
}
};



